.about {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    background-color: transparent;
    }
  
  .single-about-txt {
    max-width: 800px; /* Optimal reading width */
    text-align: justify;
    margin-bottom: 20px;
    
  }
  
  
  
  #photo {
    width: 150px;
    height: 150px;
    background-color: #ddd; /* Placeholder for photo */
    border-radius: 50%; /* Circular frame */
    margin-bottom: 20px;
    background-image: url('../components/photo.jpg'); /* Path to Abhishek's photo */
    background-size: cover;
    background-position: center;
  }
  